@import '../../../helpStyles/variables.scss';

.logOut {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 10px;
  font-weight: 400;
  letter-spacing: 0.025em;
  transition: all 0.3s linear 0s;
  svg {
    width: 18px;
    height: 18px;
    path {
      transition: all 0.3s linear 0s;
    }
  }
}

.darkMode svg path {
  stroke: #f9f9f9;
}
